import React from "react"
import { Link } from "gatsby"
//import { graphql } from "gatsby"
//		<Link to={'/playwgl/'} state={{ formfeed: true }} >
//		<Link to={"/playwgl/", {state{ formfeed: true }} >


// map name/id to file path?
//const srcstr = "/demo/bin/webgl/index.html"
const prefix = "/demo"   
//const prefix = ""

const pagefiles = prefix + "/files/"

const paths = [
prefix + "/bin/webgl/index.html",
prefix + "/bin/webgl2018/index.html"
];

const name = [
"Capsule Demo",
"Dog Demo"
];

const desc = [
"Move that capsule using the arrow keys.",
"Walk around like a dog with the arrow keys."
];

const handleLinkClick = (e, target) => {
    
	// NODE-SAFE CODE
	// Gatsby uses Node to generate our pages. 
	// Node doesn't know what a window is. 
	// Be sure to wrap any of your browser interactions
	// in some sort of node-safe if statement like this:

	if (typeof window !== 'undefined') {

		// First, are we on the home page?
		// If so, let's scroll to the desired block,
		// which was passed in as an onClick method on our <Link />.
		// If an event was also passed, we'll preventDefault()

		if (window.location.pathname === pagefiles) {
			//if (e) e.preventDefault()
			Playdemo.setIdx(target);
		}
	}
	return true;
}

class Playdemo extends React.Component {
	
	static currentIdx;
	
	static triple(n) {
		if (n===undefined) {
			n=1;
		}
		return n*3;
	}
	
	static getIdx() {
		if (this.currentIdx===undefined) {
			this.currentIdx=-1;
		}
		return this.currentIdx;
	}
	
	static setIdx(n) {
		this.currentIdx=n;
	}

	

  render() {
// don't render this component when path index is bad
    let idx = this.props.index;
    console.log(idx);
    if(idx<0||idx>=paths.length){
	return null;	
    }
	  
	  //		<Link to={"/playwgl/"}>{this.props}
	  
	let namestr = name[idx];
	let descstr = desc[idx];
	if(Playdemo.getIdx()===idx){
		this.props.enable = true;
	}else{
		this.props.enable = false;
	}
	  
	// when demo is not enabled, render a link: 
	// currently handles the click & changes static state above, rather than passing state via Gatsby Link (which is how it should be done, but doesn't work in some Gatsby versions)
	  
    if(!this.props.enable){
	return (
	<div className="playdemo">
		<Link onClick={e => handleLinkClick(e, idx)} to={'/playwgl/'} >
        <h4>Click to play {namestr} in your browser.</h4>
		<p>{descstr}</p>
		</Link>
	</div>	
	);
    }

    let srcstr = paths[idx];
    console.log(srcstr);
    return (
      <div className="playdemo">
        <h2>Play {namestr}! </h2>
        <h4>{descstr}</h4>
	<iframe src={srcstr} title="dummy" style={{width:'400px',height:'300px'}}></iframe>
      </div>
    );
  }

  oldrender() {
    return <h4>Play, {this.props.name}</h4>;
  }
}
/*		
<iframe src="/bin/webgl/index.html" title="dummy" style={{width:'400px',height:'300px'}}></iframe>
*/

export default Playdemo