import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import Playdemo from "../components/playdemo"
import Dldemo from "../components/dldemo"



class Filelist extends React.Component {

  render() {
    var data = this.props.info
    console.log(data) 

// don't render this component when Filecontrol.show=false (button controls this state)
    if(!this.props.enable){
	return null;	
    }

    return (
      <div>
        <h2>file list </h2>

	<table>
          <thead>
            <tr>
              <th>relativePath</th>
              <th>prettySize</th>
              <th>extension</th>
              <th>birthTime</th>
            </tr>
          </thead>
          <tbody>
            {data.allFile.edges.map(({ node }, index) => (
              <tr key={index}>
                <td>{node.relativePath}</td>
                <td>{node.prettySize}</td>
                <td>{node.extension}</td>
                <td>{node.birthTime}</td>
              </tr>
            ))}
          </tbody>
        </table>

      </div>
    );
  }
}		

class Filecontrol extends React.Component {
  constructor(props) {
    super(props);
    this.state = {show: false};
    this.handleToggleClick = this.handleToggleClick.bind(this);
  }

  handleToggleClick() {
    this.setState(state => ({
      show: !state.show
    }));
  }

  render() {
    let showingFiles = this.state.show;
    let element = null;
    if(showingFiles){
  	element = <button onClick={this.handleToggleClick}>Hide</button>
    }

    return (
      <div>
        <button onClick={this.handleToggleClick}>
          {showingFiles ? 'Hide' : 'Show'}
        </button>

        <Filelist info={this.props.info} enable={showingFiles} />

	{element}

      </div>
    );
  }
}

/*
Handling events with React elements is very similar to handling events on DOM elements.

in HTML:
<button onclick="activateLasers()">
  Activate Lasers
</button>
JSX:
<button onClick={activateLasers}>
  Activate Lasers
</button>

    React events are named using camelCase, rather than lowercase.
    With JSX you pass a function as the event handler, rather than a string.
*/

export default ({ data }) => {
  //console out the data youâ€™re getting from the GraphQL query 
  // so you can explore the data in your browser tools > console
  // or with gatsby build, during 'Building static HTML for pages'
 //console.log(data)  
 //<Filelist info={data} />

  return (
    <Layout>
      <div>
	<Playdemo name="Capsule Demo" index={0} enable={false}/>
	<Playdemo name="Dog Demo" index={1} enable={false}/>

	<Dldemo name="win 64 zip install"/>
	<Dldemo name="linux zip install"/>

	<Filecontrol info={data} />
   
      </div>

    </Layout>
  )
}

export const query = graphql`
  query {
    allFile {
      edges {
        node {
          relativePath
          prettySize
          extension
          birthTime(fromNow: true)
        }
      }
    }
  }
`